<template>
<div>
  <el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="学号">
    <el-input v-model="form.sno"></el-input>
  </el-form-item>
  <el-form-item label="名字">
    <el-input v-model="form.name"></el-input>
  </el-form-item>
  <el-form-item label="性别">
    <el-select v-model="form.sex" placeholder="请选择性别">
      <el-option label="男" value="男"></el-option>
      <el-option label="女" value="女"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="年龄">
    <el-input v-model="form.age"></el-input>
  </el-form-item>
  <el-form-item label="登录密码">
  <el-input v-model="form.code" show-password type="password"></el-input>
</el-form-item>
  <el-form-item label="班级名称" prop="region">
    <el-select placeholder="请选择班级名称" v-model="form.class">
      <el-option v-for="item in banji" :key="item[0]" :label="item[1]" :value="item[0]"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">添加</el-button>
    <el-button @click="exit">取消</el-button>
  </el-form-item>
</el-form>
</div>
</template>
<script>
  export default {
    data() {
      return {
        form: {
          sno: '',
          name: '',
          sex: '',
          age: '',
          class:null,
          code:''
        },
        banji:[]
      }
    },
    methods: {
      onSubmit() {
         // 检查表单信息完整性
    if (!this.form.sno || !this.form.name || !this.form.sex || !this.form.age || !this.form.class) {
    this.$message({
      type: 'warning',
      message: '请填写完整的学生信息'
    });
    return;
   }
      let params = new URLSearchParams();
      params.append('sno', this.form.sno);
      params.append('sname', this.form.name);
      params.append('ssex', this.form.sex);
      params.append('sage', this.form.age);
      params.append('class', this.form.class);
      params.append('code', this.form.code);
      
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8687/addstudent",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        if(result.data.message=="successfully"){
            this.$message({
          type: 'success',
          message: '操作成功!'
        });
          this.form.sno = '';
          this.form.name='';
          this.form.sex=null;
          this.form.age='';
          this.form.banji='';
          this.form.code=''
          
        }else{
          this.$message({
          type: 'warning',
          message: '操作失败!'
        });
        }
      }).catch((error) => {
        console.error('请求失败:', error);
      });
      },
       exit(){
         this.$router.push('/admin/student')
      }
    },
    mounted(){
       let params = new URLSearchParams();
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8687/classnum",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        this.banji = result.data; // 将获取到的课程数据存储到courses数组中
      }).catch((error) => {
        console.error('请求失败:', error);
      });
    }
    
  }
</script>
